// 
const ENV_DEVELOP = "develop";//
const ENV_TEST = "test";//
const ENV_TRIAL = "trial";//-
const ENV_RELEASE = "release";//

// develop-trial-release-
export function getEnv() {
	let accountInfo = wx.getAccountInfoSync();
	let env = accountInfo.miniProgram.envVersion ?? "develop";
	env = 'develop'
	return env
}

// apihost
export function getChatApiRequestHost() {
	let env = getEnv()
	console.log(`env=${env}`)
	switch(env) {
		case ENV_DEVELOP:
			return "http://127.0.0.1:8081"
			case ENV_TEST:
			return "https://lixp.club"
		case ENV_TRIAL:
			return "https://lixp.club"
		case ENV_RELEASE:
			return "https://lixp.club"
	}
}
// websockethost
export function getWebsocketHost() {
	let env = getEnv()
	console.log(`env=${env}`)
	switch(env) {
		case ENV_DEVELOP:
			return "ws://127.0.0.1:8081/chat"
			case ENV_TEST:
			return "wss://lixp.club/chat"
		case ENV_TRIAL:
			return "wss://lixp.club/chat"
		case ENV_RELEASE:
			return "wss://lixp.club/chat"
	}
}